import React from 'react';

const ContactForm = () => {
  return (
    <div className="h-full">
      <div className="sticky top-32 mobile:-mx-6">
        <form className="bg-primary-100 shadow-md rounded px-4 pt-6 pb-8 mb-4 text-white mobile:rounded-none mobile:px-6">
          <a
            href="https://www.cokonet.com/brochure?id=19"
            className="flex items-center justify-between bg-[#193778] rounded-md py-3 px-6"
          >
            <div className="cursor-pointer">
              <span className="text-sm text-gray-300 font-manrope">Contact</span>
              <h6 className="text-3xl font-semibold">Get In Touch</h6>
            </div>
            <div>
              <img src="https://www.cokonet.com/icons/Programoverview.svg" alt="logo" />
            </div>
          </a>
          <div className="flex items-center justify-between py-5 px-6 mobile:mb-9">
            <div>
              <span className="text-sm text-gray-300 font-manrope">
                CONTACT US
              </span>
              <h6 className="text-3xl font-semibold text-red-500">+91 86102 10593</h6>
            </div>
            <div>
              <img src="https://www.cokonet.com/icons/Programoverview.svg" alt="logo" />
            </div>
          </div>
          <div className="bg-white rounded-md px-4 py-8">
            <h6 className="font-semibold text-lg text-black pb-4">
              Enquire this Program
            </h6>
            <div className="mb-4">
              <label
                htmlFor="username"
                className="block text-gray-700 text-sm font-bold mb-2"
              >
                Name*{" "}
              </label>
              <input
                type="text"
                id="username"
                name="fullName"
                className="shadow appearance-none border rounded w-full py-3 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline"
                placeholder="Enter your name"
                fdprocessedid="1ka9he"
              />
              <p className="text-red-500 text-xs mt-1 ml-1" />
            </div>
            <div className="mb-4">
              <label
                htmlFor="email"
                className="block text-gray-700 text-sm font-bold mb-2"
              >
                Email*
              </label>
              <div className="relative">
                <input
                  type="text"
                  id="email"
                  name="email"
                  placeholder="qbee.academy@gmail.com"
                  className="shadow appearance-none border rounded w-full py-3 pl-10 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline"
                  fdprocessedid="6f2oa5"
                />
                <span className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    width={20}
                    height={20}
                    viewBox="0 0 20 20"
                    fill="none"
                  >
                    <path
                      d="M18.3346 5.00065C18.3346 4.08398 17.5846 3.33398 16.668 3.33398H3.33464C2.41797 3.33398 1.66797 4.08398 1.66797 5.00065M18.3346 5.00065V15.0007C18.3346 15.9173 17.5846 16.6673 16.668 16.6673H3.33464C2.41797 16.6673 1.66797 15.9173 1.66797 15.0007V5.00065M18.3346 5.00065L10.0013 10.834L1.66797 5.00065"
                      stroke="#667085"
                      strokeWidth="1.66667"
                      strokeLinecap="round"
                      strokeLinejoin="round"
                    />
                  </svg>
                </span>
              </div>
              <p className="text-red-500 text-xs mt-1 ml-1" />
            </div>
            <div className="mb-4">
              <label
                htmlFor="phone"
                className="block text-gray-700 text-sm font-bold mb-2"
              >
                Phone number*
              </label>
              <div className="shadow appearance-none border rounded w-full py-3 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline PhoneInput">
                <div className="PhoneInputCountry">
                  <select
                    className="PhoneInputCountrySelect"
                    aria-label="Phone number country"
                  >
                    <option value="ZZ">International</option>
                    {/* Add more options as needed */}
                  </select>
                  <div
                    className="PhoneInputCountryIcon PhoneInputCountryIcon--border"
                    aria-hidden="true"
                  >
                    <img
                      src="https://www.cokonet.com/icons/Programoverview.svg"
                      alt="India"
                      className="PhoneInputCountryIconImg"
                    />
                  </div>
                  <div className="PhoneInputCountrySelectArrow" />
                </div>
                <input
                  type="tel"
                  id="phone-input"
                  name="phoneNumber"
                  className="PhoneInputInput"
                  defaultValue="+91"
                  autoComplete="tel"
                  fdprocessedid="u1dagf"
                />
              </div>
            </div>
            <div>
              <button
                type="submit"
                className="bg-gray-300 hover:bg-blue-900 text-white font-bold py-2 px-4 rounded focus:outline-none focus:shadow-outline w-full mobile:bg-[#002169]"
                fdprocessedid="b0m78"
              >
                <span className="btn__text">Submit Enquiry</span>
                <span className="btn__icon pl-3">
                  <i className="icon-arrow p-2.5 bg-[#002169] hover:bg-blue-900 inline-flex justify-center h-[30px] w-[30px] rounded-[999px] mobile:bg-[#667085]" />
                </span>
              </button>
            </div>
          </div>
        </form>
      </div>
    </div>
  );
};

export default ContactForm;
