import React from 'react';
import img from '../../../assets/Java.webp'
const HeroSection = () => {
  return (
    <div className="bg-gray-300 mt-10 px-6 md:px-12 lg:px-24 py-16 relative grid grid-cols-12 gap-10 md:grid xl:justify-between mobile:py-0">
      <div className="col-span-7 py-10 mb-10 text-center md:mb-0 md:text-left xl:col-span-6 mobile:text-left">
        <span className="py-1 text-gray-300">CEC112</span>
        <div
          className="reveal"
          style={{
            animationFillMode: 'both',
            animationDuration: '1000ms',
            animationDelay: '0ms',
            animationIterationCount: '1',
            opacity: 1,
            animationName: 'reveal-animation',
          }}
        >
          <h2 className="text-4xl font-bold mb-8 mobile:text-left">
            SAP Materials Management
          </h2>
          <p className="text-lg mb-10 tracking-wide md:max-w-xl leading-relaxed font-normal mobile:text-left mobile:mb-7">
            This course provides hands-on training in material and procurement management in SAP S/4HANA, preparing participants for roles in procurement and materials management.
          </p>
          <div className="flex flex-col md:flex-row mobile:text-left">
            <button className="bg-white text-gray-800 font-semibold py-2 px-4 border border-gray-400 rounded shadow mb-2 md:mb-0">
              <span>Get in touch</span>
              <span className="ml-2">
                <i className="icon-arrow"></i>
              </span>
            </button>
            <a href="tel:+91 8075 400 500" className="bg-transparent text-white border border-white font-semibold py-2 px-4 rounded shadow ml-0 md:ml-3">
              <span>Talk to us</span>
            </a>
          </div>
        </div>
      </div>
      <div className="col-span-7 xl:col-span-6 py-10 mobile:relative">
        <img
          src={img}
          alt="cover-image"
          className="relative object-cover aspect-video mx-auto"
        />
      </div>
    </div>
  );
};

export default HeroSection;
