import React, { useState } from 'react';

const courseModules = [
  {
    title: 'Navigation in SAP S/4HANA',
    duration: '2 hours',
    content: 'This module emphasizes Fiori user experience...',
    topics: [
      'Identifying the SAP Fiori User Experience',
      'Working with the SAP GUI'
    ]
  },
  {
    title: 'Processes and Enterprise Structure in Procurement',
    duration: '2 hours',
    content: 'This module covers the procurement process in SAP S/4HANA...',
    topics: [
      'Delineating Procurement Processes',
      'Identifying the Organizational Units in Procurement'
    ]
  },
  {
    title: 'Plain Procurement Process',
    duration: '3 hours',
    content: 'Participants learn procurement processes in SAP S/4HANA...',
    topics: [
      'Creating a Purchase Order',
      'Posting a Goods Receipt',
      'Entering an Invoice'
    ]
  },
  {
    title: 'Master Data in Procurement',
    duration: '4 hours',
    content: 'This module focuses on mastering SAP S/4HANA\'s data management...',
    topics: [
      'Maintaining Business Partner Master Data',
      'Maintaining Material Master Data',
      'Maintaining Purchasing Info Record',
      'Analyzing Material Valuation'
    ]
  },
  {
    title: 'Stock Material Compared with Consumable Material',
    duration: '4 hours',
    content: 'This module explores procurement processes in SAP S/4HANA...',
    topics: [
      'Comparing Procurement Processes for Stock and Consumption',
      'Creating a Purchase Requisition',
      'Creating a Purchase Order with Reference to a Purchase Requisition',
      'Entering Valuated and Non-Valuated Goods Receipts'
    ]
  },
  {
    title: 'Self-Service Requisition',
    duration: '2 hours',
    content: 'This module covers self-service requisition processes in SAP S/4HANA...',
    topics: [
      'Performing a Self-Service Process',
      'Confirming a Goods Receipt'
    ]
  },
  {
    title: 'Advanced Transactions in Procurement',
    duration: '11 hours',
    content: 'This module covers advanced procurement transactions in SAP S/4HANA...',
    topics: [
      'Maintaining a Purchasing Contract',
      'Creating a Purchase Requisition with Source Determination',
      'Using Advanced Transaction for Purchase Order Creation',
      'Using the Advanced Transaction for Goods Receipt',
      'Using the Advanced Transaction for Invoice Verification',
      'Special Procurement Scenarios',
      'Physical Inventory Process',
      'Outline Agreement',
      'Request for Quotation',
      'Good Issue and Transfer Posting',
      'Reservations'
    ]
  }
];

const SAPCourse = () => {
  const [openModule, setOpenModule] = useState(null);

  const toggleModule = (index) => {
    setOpenModule(openModule === index ? null : index);
  };

  return (
    <div className="my-12">
      <div className="flex items-center pb-3 gap-7 mt-14 mobile:pb-0">
        <div className="flex items-center gap-6 text-[#0A044B]">
          <img src="https://www.cokonet.com/icons/Programoverview.svg" alt="Course-modules" />
          <h3 className="py-3 text-[32px] mobile:text-[21px]">
            Course Modules
          </h3>
        </div>
        <div className="py-2 px-5 bg-[#4D6496] rounded-full mobile:px-1">
          <p className="uppercase text-gray-300 text-sm mobile:text-[10px]">
            60-80 Days COURSE
          </p>
        </div>
      </div>
      <div className="lg:ml-14">
        {courseModules.map((module, index) => (
          <div key={index} className="course-item py-4 px-6 rounded-lg border border-gray-200 hover:border-primary-100 hover:bg-gray-50 cursor-pointer">
            <div
              className="text-center text-gray-400 uppercase text-base font-medium pb-2 mobile:text-[10px]"
              onClick={() => toggleModule(index)}
            >
              {module.duration}
            </div>
            <div className="title flex justify-between items-center" onClick={() => toggleModule(index)}>
              <h2 className="mb-0 text-2xl font-semibold mobile:text-[21px]">
                {module.title}
              </h2>
              <span className="text-primary-100 text-3xl">
                <span>{openModule === index ? '-' : '+'}</span>
              </span>
            </div>
            {openModule === index && (
              <div className="course-content mt-4">
                <div className="item-inner text-[#4D6496] max-w-[400px] mobile:text-sm">
                  {module.content}
                </div>
                <div className="stepper_wrapper mt-4">
                  <div className="c-stepper bg-white mb-5 py-6 px-4 rounded-lg flex justify-between items-center">
                    <div className="w-full">
                      <ul>
                        {module.topics.map((topic, idx) => (
                          <li key={idx} className="c-stepper__item pb-2">
                            <h3 className="c-stepper__title">{topic}</h3>
                            <label className="font-manrope absolute right-0">
                              1 H
                            </label>
                          </li>
                        ))}
                      </ul>
                    </div>
                  </div>
                </div>
              </div>
            )}
          </div>
        ))}
      </div>
    </div>
  );
};

export default SAPCourse;
