import React from 'react';

const HeroDetails = () => {
  return (
    <section className="bg-[#E8EEF3]">
      <div className="container mx-auto flex flex-col md:flex-row gap-6 py-8 md:py-11">
        {/* Duration */}
        <div className="flex items-center gap-5 flex-1 ml-12">
          <img src="https://www.cokonet.com/icons/Programoverview.svg" alt="course-dot" className="w-6 h-auto" />
          <div>
            <p className="uppercase text-sm font-semibold font-manrope text-[#4D6496] tracking-[2px]">
              Duration
            </p>
            <p className="text-lg text-primary-100">60-80 Days</p>
          </div>
        </div>

        {/* Next Batch */}
        <div className="flex items-center gap-5 flex-1">
          <img src="https://www.cokonet.com/icons/Programoverview.svg" alt="course-dot" className="w-6 h-auto" />
          <div>
            <p className="uppercase text-sm font-semibold font-manrope text-[#4D6496] tracking-[2px]">
              Next Batch
            </p>
            <p className="text-lg text-primary-100">May 22, 2024</p>
          </div>
        </div>

        {/* Mode */}
        <div className="flex items-center gap-5 flex-1">
          <img src="https://www.cokonet.com/icons/Programoverview.svg" alt="course-dot" className="w-6 h-auto" />
          <div>
            <p className="uppercase text-sm font-semibold font-manrope text-[#4D6496] tracking-[2px]">
              Mode
            </p>
            <p className="text-lg text-primary-100">Both</p>
          </div>
        </div>

        {/* Payment Options */}
        <div className="flex items-center gap-5 flex-1">
          <img src="https://www.cokonet.com/icons/Programoverview.svg" alt="course-dot" className="w-6 h-auto" />
          <div>
            <p className="uppercase text-sm font-semibold font-manrope text-[#4D6496] tracking-[2px]">
              Payment Options
            </p>
            <div className="flex items-center gap-2">
             
              <p className="text-lg text-primary-100">Available</p>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default HeroDetails;
