import React from 'react';

const EligibilitySection = () => {
  return (
    <div className="border-b-[1px] pb-10">
      <div className="flex items-center gap-3 pb-3 text-primary-100">
        <div className="flex items-start gap-6">
          <img src="https://www.cokonet.com/icons/Programoverview.svg" alt="eligibility-icon" />
          <div className="flex flex-col">
            <h3 className="pb-2 text-[32px] mobile:text-[21px] mobile:pb-6">
              Eligibility
            </h3>
            <p className="text-[#144467] text-lg pb-10 font-medium mobile:text-sm">
              Diverse backgrounds welcomed. While familiarity with materials
              management is beneficial, individuals from Business, Humanities,
              and Sciences can excel, paving the way for a successful SAP MM
              career.
            </p>
          </div>
        </div>
      </div>
      <div className="flex items-center gap-3 pb-3 text-primary-100">
        <div className="flex items-start gap-3">
          <img src="https://www.cokonet.com/icons/Programoverview.svg" alt="learn-icon" />
          <div className="flex flex-col">
            <h3 className="pb-2 text-[32px] mobile:text-[21px] mobile:pb-6">
              Why should you learn?
            </h3>
            <p className="text-[#144467] text-lg pb-10 font-medium mobile:text-sm">
              Studying this course enables specialization in procurement,
              inventory, and warehouse management using SAP ERP.
            </p>
          </div>
        </div>
      </div>
    </div>
  );
};

export default EligibilitySection;
